h3 {
    margin-top: 100px;
    text-align: center;
    padding: 30px 0;
    font-size: 30px;
}
.tour__list {
    max-width: 767px;
    width: calc(100% - 30px);
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    .item {
        width: calc(100% / 2 - 10px);
        word-break: keep-all;
        margin-bottom: 20px;
        position: relative;
        border-radius: 20px 20px 20px 20px;
        overflow: hidden;
        line-height: 1.3;
        box-shadow: 0px 20px 10px ;
        h4 {
            font-size: 20px;
            
            padding: 20px;
            position: absolute;
            background-color: rgba(0, 0, 0, 0.459);
            color: white;
            width: 100%;
        }

        figure {
            height: 65.18vw;
            border-radius: 0 0 50px 50px;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                           }
        }
        div {
            padding: 20px 10px;
            p{
              font-size: 18px;
              margin-bottom: 10px;
              font-weight: bold;
            }
            span {
                position: relative;
                display: flex;
                align-items: start;
                font-size: 14px;
                
            }
            span::before {
                content: "";
                background: url("../public/img/location-red.png") center / 100%
                    no-repeat;
                display: inline-block;
                width:  10px;
                height: 20px;
                line-height: 20px;
                margin-right: 8px;
            }
        }
    }
}

@media screen and (max-width: 550px) {
  .tour__list{
    .item{
      
      figure{
        height: 50vw;
      }
    }
  }
}
@media screen and (max-width: 400px){
  .tour__list{
    .item{
      width: calc(100%)
    }
  }
}
