@font-face {
  font-family: 'KoPubWorldDotum';
  src: url('font/KoPubWorldDotumBold.eot');
  src: url('font/KoPubWorldDotumBold.eot?#iefix') format('embedded-opentype'),
      url('font/KoPubWorldDotumBold.woff2') format('woff2'),
      url('font/KoPubWorldDotumBold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'KoPubWorldDotum';
  src: url('font/KoPubWorldDotumMedium.eot');
  src: url('font/KoPubWorldDotumMedium.eot?#iefix') format('embedded-opentype'),
      url('font/KoPubWorldDotumMedium.woff2') format('woff2'),
      url('font/KoPubWorldDotumMedium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'KoPubWorldDotum';
  src: url('font/KoPubWorldDotumLight.eot');
  src: url('font/KoPubWorldDotumLight.eot?#iefix') format('embedded-opentype'),
      url('font/KoPubWorldDotumLight.woff2') format('woff2'),
      url('font/KoPubWorldDotumLight.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}


*,
::after,
::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
button {
    background-color: transparent;
    border: 0;
    cursor: pointer;
    font-weight: bold;
}
body {
    line-height: 1;
    color: #333;
    font-family: 'KoPubWorldDotum';
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background-color: #f2f5f6;
    z-index: 9999;
    .inner {
        position: relative;
        max-width: 767px;
        width: calc(100% - 30px);
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        h1 {
            img {
                height: 90px;
            }
        }
        div {
            display: flex;
            align-items: center;
            gap: 10px;
            .like--btn {
                img {
                    height: 30px;
                }
            }
            .menu--btn {
                border: 1px solid #333;
                padding: 10px 15px;
                border-radius: 20px;
                background-color: #fff;
                span {
                    display: flex;
                    align-items: center;
                    img {
                        height: 15px;
                        margin-left: 10px;
                    }
                }
            }
            .nav--mobile {
                display: none;
                z-index: 999;
                position: absolute;
                top: 80px;
                right: 0;
                .gnb {
                    font-weight: bold;
                    padding: 20px;
                    border: 1px solid #f2f5f6;
                    border-radius: 20px;
                    background-color: #fff;
                    margin-bottom: 10px;
                    li {
                        padding: 20px 0;
                    }
                    li::after{
                      content: '';
                      background: url() center/ 100% no-repeat;;
                      width: 20px;
                      height: 20px;
                    }
                }
                .gnb02 {
                    display: flex;
                    flex-direction: column;
                    p {
                        margin-bottom: 20px;
                    }
                    input {
                        padding: 10px 5px;
                        width: 100%;
                        border-radius: 20px;
                        border: 1px solid grey;
                    }
                }
            }
            .nav--mobile.on {
                display: block;
            }
        }
    }
}
